import React from 'react';
import classname from 'classnames'
import { MdPerson } from 'react-icons/md'
import styles from './templates.module.scss'

console.log(styles);

function Card({icon: Icon, iconProps, children}) {
   return (
      <div 
         className={
            classname(
               styles.card, 
               styles.grid, 
               styles.centerEverything
            )
         }
      > 
         <div><Icon {...iconProps} /></div>
         <div>
            {children}
         </div>
      </div>
   )
}

function PresentImage ({children, size, ...another}) {
   return (
      <div
         {...another} 
         className={classname(another.className, styles.image)} 
         style={{...another.style, height: size + 'px'}}
      >
         {children}
      </div>
   )
}

const Center = props => (
   <div className={classname(props.className, styles.center)} {...props}>
      {props.children}
   </div>
)

class Templates extends React.Component {
   render() {
      return (
         <div>
            <PresentImage size={200} style={{padding: 15}}>
               <Center>Lol I am surprised</Center>
            </PresentImage>
            <div className={styles.gridC2}>
               <div>
                  Very good history
               </div>
               <PresentImage />
            </div>
            <div className={styles.gridC2}>
               <PresentImage />
               <div>
                  Very good history
               </div>
            </div>
         </div>
      );
   }
}

export default Templates;